    export default {
        name: "search-input",
	    methods: {
            route_jump(data) {
                if (this.$user.isLogin() && data === '/plugins/advance/order/order') {
                    uni.navigateTo({
                        url: data,
                    })
                } else if (!this.$user.isLogin() && data === '/plugins/advance/order/order') {
                    this.$user.getInfo().then(info => {
                        uni.navigateTo({
                            url: data,
                        })
                    }).catch(error => {
                    });
                } else {
                    uni.navigateTo({
                        url: data,
                    })
                }
            }
	    },

		props: {
			theme: Object,
		}
    }
